import React from 'react'
import './Proxy.css'

export default props => {

  // コンテンツ作成
  const cards = props.state.map(
    value => (
      <div key={value.id} className="frame">
        <img src={value.url} alt="" className="card"/>
        <input className="input"
          onChange={node => props.changeUrl(value.id, node.target.value)}/>
        <button className="deleteButton" onClick={() => props.deleteCard(value.id)}> X </button>
      </div>
    )
  )

  // 印刷範囲指定
  let args = []
  let arg = []
  for (let i = 0; i < cards.length; i++) {
    arg = [...arg, cards[i]]
    if (arg.length === 9 || i === cards.length - 1) {
      args = [...args, arg]
      arg = []
    }
  }

  const pages = args.map(
    (value, index) => (
      <div key={index} className="printFrame">
        {value}
      </div>
    )
  )

  return (
    <>
      {pages}
      {props.addCardDispFlg &&
        <div className="button-div">
          <button className="addCard" onClick={props.addCard}> ADD CARD </button>
        </div>
      }
    </>
  )
}
